<script>
export default {
  name: 'UserInfoView',
  data() {
    return {
      searchQuery: {
        id: '',
        name: '',
        position: '',
        status: '',
        startDate: '2025-02-24',
        endDate: '2025-02-26'
      },
      currentPage: 1,
      pageSize: 10,
      total: 100,
      selectedRows: [],
      dialogVisible: false,
      dialogTitle: '',
      userForm: {
        id: '',
        name: '',
        gender: '',
        department: '',
        status: '',
        contactMethod: '',
        createTime: ''
      },
      rules: {
        id: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 10, message: ' 2  10 ', trigger: 'blur' }
        ],
        department: [
          { required: true, message: '', trigger: 'change' }
        ],
        contactMethod: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^1[3-9]\d{9}$/, message: '', trigger: 'blur' }
        ]
      },
      tableData: [
        {
          id: '001',
          name: '',
          gender: '',
          department: '',
          status: '',
          contactMethod: '18145674589',
          createTime: '2025-03-03'
        },
        {
          id: '002',
          name: '',
          gender: '',
          department: '',
          status: '',
          contactMethod: '13812345678',
          createTime: '2025-03-04'
        },
        {
          id: '003',
          name: '',
          gender: '',
          department: '',
          status: '',
          contactMethod: '15987654321',
          createTime: '2025-03-05'
        },
        {
          id: '004',
          name: '',
          gender: '',
          department: '',
          status: '',
          contactMethod: '17698765432',
          createTime: '2025-03-06'
        },
        {
          id: '005',
          name: '',
          gender: '',
          department: '',
          status: '',
          contactMethod: '13567891234',
          createTime: '2025-03-07'
        }
      ]
    }
  },
  methods: {
    goBack() {
      this.$router.push('/battle-simulation')
    },
    handleSearch() {
      console.log('Search with:', this.searchQuery)
      // API
    },
    handleAdd() {
      this.dialogTitle = ''
      this.userForm = {
        id: '',
        name: '',
        gender: '',
        department: '',
        status: '',
        contactMethod: '',
        createTime: new Date().toISOString().split('T')[0]
      }
      this.dialogVisible = true
    },
    handleEdit(row) {
      this.dialogTitle = ''
      if (row) {
        this.userForm = JSON.parse(JSON.stringify(row))
      } else if (this.selectedRows.length === 1) {
        this.userForm = JSON.parse(JSON.stringify(this.selectedRows[0]))
      } else {
        this.$message.warning('')
        return
      }
      this.dialogVisible = true
    },
    handleDelete(row) {
      console.log('Delete user:', row || this.selectedRows)
    },
    handleImport() {
      console.log('Import users')
    },
    handleExport() {
      console.log('Export users')
    },
    handleGroupManage() {
      console.log('Manage user groups')
    },
    handleSizeChange(val) {
      this.pageSize = val
      console.log('Page size changed to:', val)
    },
    handleCurrentChange(val) {
      this.currentPage = val
      console.log('Current page changed to:', val)
    },
    handleSelectionChange(selection) {
      this.selectedRows = selection
      console.log('Selection changed:', selection)
    },
    submitForm() {
      this.$refs.userForm.validate((valid) => {
        if (valid) {
          if (this.dialogTitle === '') {
            // 
            this.tableData.unshift({
              ...this.userForm,
              createTime: new Date().toISOString().split('T')[0]
            })
            this.$message.success('')
          } else {
            // 
            const index = this.tableData.findIndex(item => item.id === this.userForm.id)
            if (index !== -1) {
              this.tableData.splice(index, 1, this.userForm)
              this.$message.success('')
            }
          }
          this.dialogVisible = false
        } else {
          return false
        }
      })
    },
    resetForm() {
      this.$refs.userForm && this.$refs.userForm.resetFields()
    },
  }
}
</script>

