import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/views/Login.vue'
import Register from '@/views/Register.vue'
import Dashboard from '@/views/dashboard.vue'
import MainLayout from '@/layouts/MainLayout.vue'
import UserInfo from '@/views/user/UserInfo.vue'
import Organization from '@/views/user/Organization.vue'
import Roles from '@/views/user/Roles.vue'
import OperationLogs from '@/views/user/OperationLogs.vue'
import ForgotPassword from '@/views/ForgotPassword.vue'
import BattleSimulation from '@/views/dashboard/BattleSimulation.vue'
import DeviceBinding from '@/views/tasks/DeviceBinding.vue'
import TaskAssignment from '@/views/tasks/TaskAssignment.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: {
      requiresAuth: false,
      title: ''
    }
  },
  {
    path: '/register',
    name: 'Register',
    component: Register,
    meta: {
      requiresAuth: false,
      title: ''
    }
  },
  {
    path: '/forgot-password',
    name: 'ForgotPassword',
    component: ForgotPassword,
    meta: {
      requiresAuth: false,
      title: ''
    }
  },
  
  {
    path: '/dashboard',
    name: 'AdminDashboard',
    component: Dashboard,
    meta: {
      requiresAuth: true,
      title: '',
      roles: [0, 1] // 0-, 1-
    }
  },
  {
    path: '/battle-simulation',
    name: 'BattleSimulation',
    component: BattleSimulation,
    meta: {
      requiresAuth: true,
      title: '',
      roles: [0, 1] // 
    }
  },
  {
    path: '/user',
    component: MainLayout,
    meta: {
      requiresAuth: true
    },
    children: [
      {
        path: 'info',
        name: 'UserInfo',
        component: UserInfo,
        meta: {
          requiresAuth: true,
          title: '',
          roles: [0, 1] // 0-, 1-
        }
      },
      {
        path: 'organization',
        name: 'Organization',
        component: Organization,
        meta: {
          requiresAuth: true,
          title: '',
          roles: [0] // 
        }
      },
      {
        path: 'roles',
        name: 'Roles',
        component: Roles,
        meta: {
          requiresAuth: true,
          title: '',
          roles: [0] // 
        }
      },
      {
        path: 'logs',
        name: 'OperationLogs',
        component: OperationLogs,
        meta: {
          requiresAuth: true,
          title: '',
          roles: [0] // 
        }
      }
    ]
  },
  {
    path: '/tasks',
    component: MainLayout,
    meta: {
      requiresAuth: true
    },
    children: [
      {
        path: 'device-binding',
        name: 'DeviceBinding',
        component: DeviceBinding,
        meta: {
          requiresAuth: true,
          title: '',
          roles: [0, 1] // 0-, 1-
        }
      },
      {
        path: 'task-assignment',
        name: 'TaskAssignment',
        component: TaskAssignment,
        meta: {
          requiresAuth: true,
          title: '',
          roles: [0, 1] // 0-, 1-
        }
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

// 
router.beforeEach((to, from, next) => {
  console.log('[] :', from.fullPath, '', to.fullPath);

  const token = localStorage.getItem('token');
  const userInfoStr = localStorage.getItem('userInfo');

  if (to.path === '/login' && token) {
    console.log('[]  /battle-simulation');
    return next('/battle-simulation');
  }

  // 
  if (to.matched.some(record => record.meta.requiresAuth === false)) {
    return next();
  }

  // 
  if (!token) {
    console.log('[] ');
    return next({
      path: '/login',
      query: { redirect: to.fullPath }
    });
  }

  // token  userInfo 
  if (!userInfoStr) {
    console.warn('[] token  userInfo ');
    return next(); //  
  }

  const userInfo = JSON.parse(userInfoStr);
  const userRole = userInfo.role;

  if (!to.meta.roles || to.meta.roles.includes(userRole)) {
    return next();
  } else {
    console.warn('[] ');
    return next('/battle-simulation');
  }
});



export default router
