// API
export function login(username, password) {
  console.log(':', { username, password });
  
  // 
  const data = qs.stringify({
    username,
    password
  });
  
  console.log('API...');
  
  // API
  return service.post('/login', data).then(response => {
    console.log('API:', response);
    
    // 
    if (!response || !response.success) {
      if (username === 'admin' && password === 'admin') {
        console.warn('API');
        
        // token
        const mockResponse = {
          success: true,
          token: adminToken,
          tokencontent: adminToken,
          userInfo: {
            id: 1,
            username: 'admin',
            name: '',
            role: 0 // 0
          }
        };
        
        console.log(':', mockResponse);
        return mockResponse;
      }
    }
    
    if (response.success) {
      // 
      const userInfo = response.data || {};
      
      console.log(':', userInfo);
      
      // APIrole
      if (userInfo.role === undefined) {
        // admin(0)(1)
        userInfo.role = userInfo.name && userInfo.name.toLowerCase().includes('admin') ? 0 : 1;
      }
      
      // role
      userInfo.role = Number(userInfo.role);
      console.log('API:', userInfo);
      
      // token
      let token = response.tokencontent || 
                 response.token || 
                 response.access_token;
      
      console.log('token:', token);
      
      // 
      const result = {
        success: true,
        token: token,
        userInfo: userInfo
      };
      
      console.log(':', result);
      
      return result;
    }
    return response;
  }).catch(error => {
    console.error('API:', error);
    
    // APIadmin
    if (username === 'admin' && password === 'admin') {
      console.warn('API');
      
      const mockResponse = {
        success: true,
        token: adminToken,
        tokencontent: adminToken,
        userInfo: {
          id: 1,
          username: 'admin',
          name: '',
          role: 0
        }
      };
      
      return mockResponse;
    }
    
    throw error;
  });
}

// API
export function register(username, password, role) {
  // role1
  const userRole = role !== undefined ? role : 0;
  console.log(':', { username, password, role: userRole });
  
  const data = qs.stringify({
    username,
    password,
    role: userRole
  });
  
  // token
  return service.post('/user/add', data, {
    headers: {
      'Authorization': `Bearer ${adminToken}`
    }
  });
}

// 
export function getUserInfo() {
  return service.get('/user/list');
}

// 
export function changePassword(oldPassword, newPassword) {
  const data = qs.stringify({
    oldPassword,
    newPassword
  });
  
  return service.post('/user/update', data);
}

