.help-panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.help-panel-content {
  font-size: 14px;
  line-height: 1.5;
}

.help-panel-content ul {
  padding-left: 20px;
  margin-top: 5px;
}

.help-panel-content li {
  margin-bottom: 5px;
}

/*  */
.submenu {
  background-color: rgba(30, 40, 50, 0.9);
  margin-left: 20px;
  border-left: 1px solid rgba(58, 68, 82, 0.8);
}

.submenu-item {
  padding: 8px 15px;
  cursor: pointer;
  transition: background-color 0.3s;
  color: #ccc;
  font-size: 13px;
  border-bottom: 1px solid rgba(58, 68, 82, 0.3);
}

.submenu-item:hover {
  background-color: rgba(58, 68, 82, 1);
  color: #fff;
}

/*  */
.label-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}

.model-label {
  position: absolute;
  background-color: rgba(0, 20, 40, 0.8);
  color: white;
  padding: 5px 10px;
  border-radius: 4px;
  font-size: 12px;
  white-space: nowrap;
  pointer-events: none;
  border: 1px solid #00a8ff;
  transform: translate(-50%, -100%);
  margin-top: -10px;
}

.card-container {
  position: absolute;
  top: 20px;
  left: 20px;
  z-index: 10;
  width: 80%;
  max-width: 1200px;
}

.card-content {
  background-color: transparent;
  padding: 0;
  border-radius: 0;
  box-shadow: none;
}
.top-border{
  width: 100%;
  height: 60px;
  background: url('../../assets/top.png') no-repeat center;
  background-size: 100% 60px;
  background-color: #3a4452;
}
