      active-text-color="#ffd04b"
      :collapse="isCollapse"
      :unique-opened="true"
      router
    >
      <div class="logo-container">
        <img src="../assets/logo.png" class="logo" alt="Logo">
        <span v-if="!isCollapse" class="title"></span>
      </div>

      <!--  -->
      <el-submenu index="/user">
        <template slot="title">
          <i class="el-icon-user"></i>
          <span></span>
        </template>
        <!--  -  -->
        <el-menu-item index="/user/info"></el-menu-item>
        <!--  -->
        <el-menu-item v-if="isAdmin" index="/user/organization"></el-menu-item>
        <el-menu-item v-if="isAdmin" index="/user/roles"></el-menu-item>
        <el-menu-item v-if="isAdmin" index="/user/logs"></el-menu-item>
      </el-submenu>

      <!--  -->
      <el-submenu index="/device">
        <template slot="title">
          <i class="el-icon-monitor"></i>
          <span></span>
        </template>
        <el-menu-item index="/device/info"></el-menu-item>
      </el-submenu>

      <!--  -->
      <el-submenu index="/tasks">
        <template slot="title">
          <i class="el-icon-s-order"></i>
          <span></span>
        </template>
        <el-menu-item index="/tasks/device-binding"></el-menu-item>
        <el-menu-item index="/tasks/task-assignment"></el-menu-item>
      </el-submenu>

      <!--  -->
      <el-menu-item index="/battle-simulation">
        <i class="el-icon-map-location"></i>
        <span></span>
      </el-menu-item>

      <!--  -->
      <el-submenu index="/course">
        <template slot="title">
          <i class="el-icon-reading"></i>
          <span></span>
        </template>
        <el-menu-item index="/course/info"></el-menu-item>
        <el-menu-item index="/course/category"></el-menu-item>
        <el-menu-item index="/course/publish"></el-menu-item>
