</template>

<script>
export default {
  name: 'Sidebar',
  data() {
    return {
      isCollapse: false,
      activeIndex: this.$route.path,
      userInfo: null
    }
  },
  computed: {
    // 
    isAdmin() {
      console.log('isAdmin:', this.userInfo);
      // =====
      return this.userInfo && this.userInfo.role == 0;
    }
  },
  created() {
    // 
    this.getUserInfo();
    
    // 
    window.addEventListener('storage', this.handleStorageChange);
  },
  beforeDestroy() {
    // 
    window.removeEventListener('storage', this.handleStorageChange);
  },
  methods: {
    // 
    getUserInfo() {
      const userInfoStr = localStorage.getItem('userInfo');
      
      console.log('localStorage:', userInfoStr);
      
      if (userInfoStr) {
        try {
          this.userInfo = JSON.parse(userInfoStr);
          console.log(':', this.userInfo.role);
        } catch (error) {
          console.error(':', error);
          this.userInfo = null;
        }
      } else {
        console.log('localStorageuserInfo');
      }
    },
    // 
    handleStorageChange(e) {
      if (e.key === 'userInfo') {
        this.getUserInfo();
      }
    }
  },
  watch: {
    $route(route) {
      this.activeIndex = route.path;
    }
  }
}
</script>

