<template>
  <div class="roles-container">
    <div class="page-header">
      <div class="header-left">
        <div class="back-button" @click="goBack">
          <i class="el-icon-arrow-left"></i> 
        </div>
        <h2 class="page-title"></h2>
      </div>
      <!-- MainLayout -->
    </div>

    <div class="search-form">
      <el-form :inline="true" :model="searchQuery" class="demo-form-inline">
        <el-form-item>
          <el-input v-model="searchQuery.name" placeholder="" size="medium" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-input v-model="searchQuery.realName" placeholder="" size="medium" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-select v-model="searchQuery.status" placeholder="" size="medium" clearable>
            <el-option label="" :value="''"></el-option>
            <el-option label="" :value="''"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSearch" size="medium" class="search-btn"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="toolbar">
      <div>
        <el-button type="primary" class="action-btn" @click="showAddUserDialog">
        <i class="el-icon-plus"></i> 
      </el-button>
      </div>
      
      <!--  -->
    <div style="padding: 0 20px 0 20px;">
      <el-table
      :data="userList"
      border
      style="width: 100%"
      v-loading="loading"
      @selection-change="handleSelectionChange"
    >
      <el-table-column
        type="selection"
        width="55"
      ></el-table-column>
      <el-table-column
        prop="id"
        label="ID"
        width="80"
      ></el-table-column>
      <el-table-column
        prop="name"
        label=""
        width="150"
      ></el-table-column>
      <el-table-column
        prop="password"
        label=""
        width="260"
      ></el-table-column>
      <!-- <el-table-column
        prop="department"
        label=""
        width="150"
      ></el-table-column> -->
      <!-- <el-table-column
        prop="contactMethod"
        label=""
        width="150"
      ></el-table-column> -->
      <el-table-column
        prop="role"
        label=""
        width="100"
      >
        <template slot-scope="scope">
          {{ scope.row.role === '0' ? '0' : '1' }}
        </template>
      </el-table-column>
      <el-table-column
        prop="createtime"
        label=""
      ></el-table-column>
      <el-table-column
        label=""
        width="300"
      >
        <template slot-scope="scope">
          <span class="edit-btn" @click="handleEdit(scope.row)">
            <i class="el-icon-edit"></i> 
          </span>
          <span class="edit-btn" @click="handleChangePassword(scope.row)" style="margin-left: 10px;">
            <i class="el-icon-key"></i> 
          </span>
          <span class="delete-btn" @click="handleDelete(scope.row)">
            <i class="el-icon-delete"></i> 
          </span>
        </template>
      </el-table-column>
    </el-table>
    </div>
   

    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
    </div>

    <!-- / -->
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="500px" @close="resetForm">
      <el-form :model="userForm" :rules="rules" ref="userForm" label-width="100px" class="user-form">
        <el-form-item label="" prop="name">
          <el-input v-model="userForm.name" placeholder="" size="medium" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="realName">
          <el-input v-model="userForm.realName" placeholder="" size="medium" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="department">
          <el-input v-model="userForm.department" placeholder="" size="medium" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="contactMethod">
          <el-input v-model="userForm.contactMethod" placeholder="" size="medium" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select v-model="userForm.status" placeholder="" size="medium" clearable>
            <el-option label="" :value="''"></el-option>
            <el-option label="" :value="''"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item v-if="!isEdit" label="" prop="password">
          <el-input type="password" v-model="userForm.password" placeholder="" size="medium" show-password></el-input>
        </el-form-item>
        <el-form-item v-if="!isEdit" label="" prop="confirmPassword">
          <el-input type="password" v-model="userForm.confirmPassword" placeholder="" size="medium" show-password></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false" size="medium"> </el-button>
        <el-button type="primary" @click="submitForm" size="medium"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="passwordDialogVisible" width="500px" @close="resetPasswordForm">
      <el-form :model="passwordForm" :rules="passwordRules" ref="passwordForm" label-width="100px" class="password-form">
        <el-form-item label="" prop="newPassword">
          <el-input type="password" v-model="passwordForm.newPassword" placeholder="" size="medium" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirmPassword">
          <el-input type="password" v-model="passwordForm.confirmPassword" placeholder="" size="medium" show-password></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="passwordDialogVisible = false" size="medium"> </el-button>
        <el-button type="primary" @click="submitPasswordForm" size="medium"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Cookies from 'js-cookie';

export default {
  name: 'Roles',
  data() {
    // 
    const validateConfirmPassword = (rule, value, callback) => {
      if (value !== this.userForm.password) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };

    // 
    const validatePasswordConfirm = (rule, value, callback) => {
      if (value !== this.passwordForm.newPassword) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };

    return {
      // 
      searchQuery: {
        name: '',
        realName: '',
        status: ''
      },
      
      // 
      userList: [],
      loading: false,
      total: 0,
      currentPage: 1,
      pageSize: 10,
      selectedUsers: [],

      // 
      dialogVisible: false,
      dialogTitle: '',
      isEdit: false,
      currentUserId: null,

      // 
      userForm: {
        name: '',
        realName: '',
        department: '',
        contactMethod: '',
        status: '',
        password: '',
        confirmPassword: ''
      },

      // 
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
        ],
        realName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        department: [
          { required: true, message: '', trigger: 'blur' }
        ],
        contactMethod: [
          { required: true, message: '', trigger: 'blur' }
        ],
        status: [
          { required: true, message: '', trigger: 'change' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validateConfirmPassword, trigger: 'blur' }
        ]
      },

      // 
      passwordDialogVisible: false,
      passwordForm: {
        newPassword: '',
        confirmPassword: ''
      },
      passwordRules: {
        newPassword: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validatePasswordConfirm, trigger: 'blur' }
        ]
      }
    };
  },
  created() {
    console.log('Roles');
    this.fetchUserList();
  },
  methods: {
    // 
    goBack() {
      this.$router.go(-1);
    },
    
    // 
    async fetchUserList() {
      try {
        this.loading = true;
        
        //  - 
        const params = {
          currentpage: this.currentPage,
          perpagecount: this.pageSize
        };
        
        console.log(':', params);
        
        //  cookie  token
        const token = Cookies.get('Authorization') || Cookies.get('token');
        console.log(' cookie  token:', token);
        
        if (!token) {
          console.error(' token');
          this.$message.error('');
          this.$router.push('/login');
          return;
        }
        
        // axios
        const axios = require('axios');
        
        // 
        const config = {
          headers: { 
            'Authorization': token, //  cookie  token Bearer 
            'Content-Type': 'application/json'
          },
          withCredentials: true //  cookie
        };
        
        console.log(':', config);
        
        // 
        const response = await axios.post(
          'http://117.50.115.177:8991/user/list',
          params,
          config
        );
        
        console.log(':', response);
        
        // 
        if (response) {
          this.userList = response.data.data;
          this.total = response.rowcount || 0;
          console.log(':', this.userList);
        } else {
          console.error(':', response.data);
          this.$message.error(': ');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error(': ' + (error.message || ''));
      } finally {
        this.loading = false;
      }
    },

    // 
    handleSearch() {
      this.currentPage = 1;
      this.fetchUserList();
    },

    // 
    handleSizeChange(val) {
      this.pageSize = val;
      this.fetchUserList();
    },
    
    handleCurrentChange(val) {
      this.currentPage = val;
      this.fetchUserList();
    },
    
    // 
    handleSelectionChange(selection) {
      this.selectedUsers = selection;
    },

    // 
    showAddUserDialog() {
      this.dialogTitle = '';
      this.isEdit = false;
      this.userForm = {
        name: '',
        realName: '',
        department: '',
        contactMethod: '',
        status: '',
        password: '',
        confirmPassword: ''
      };
      this.dialogVisible = true;
      // DOM
      this.$nextTick(() => {
        if (this.$refs.userForm) {
          this.$refs.userForm.clearValidate();
        }
      });
    },

    // 
    handleEdit(row) {
      this.dialogTitle = '';
      this.isEdit = true;
      this.currentUserId = row.id;
      this.userForm = {
        name: row.name,
        realName: row.realName,
        department: row.department,
        contactMethod: row.contactMethod,
        status: row.status
      };
      this.dialogVisible = true;
      // DOM
      this.$nextTick(() => {
        if (this.$refs.userForm) {
          this.$refs.userForm.clearValidate();
        }
      });
    },

    // 
    handleChangePassword(row) {
      this.currentUserId = row.id;
      this.passwordForm = {
        newPassword: '',
        confirmPassword: ''
      };
      this.passwordDialogVisible = true;
      // DOM
      this.$nextTick(() => {
        if (this.$refs.passwordForm) {
          this.$refs.passwordForm.clearValidate();
        }
      });
    },

    // 
    handleDelete(row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // 
        const index = this.userList.findIndex(item => item.id === row.id);
        if (index !== -1) {
          this.userList.splice(index, 1);
          this.$message.success('');
        }
      }).catch(() => {
        // 
      });
    },

    // 
    submitForm() {
      this.$refs.userForm.validate(valid => {
        if (valid) {
          const formData = { ...this.userForm };
          // 
          delete formData.confirmPassword;
          
          if (this.isEdit) {
            // 
            const index = this.userList.findIndex(item => item.id === this.currentUserId);
            if (index !== -1) {
              this.userList[index] = { ...this.userList[index], ...formData };
              this.$message.success('');
              this.dialogVisible = false;
            }
          } else {
            // 
            const newUser = {
              ...formData,
              id: this.userList.length + 1
            };
            this.userList.push(newUser);
            this.$message.success('');
            this.dialogVisible = false;
          }
        } else {
          return false;
        }
      });
    },

    // 
    submitPasswordForm() {
      this.$refs.passwordForm.validate(valid => {
        if (valid) {
          // 
          this.$message.success('');
          this.passwordDialogVisible = false;
        } else {
          return false;
        }
      });
    },
    
    // 
    resetForm() {
      if (this.$refs.userForm) {
        this.$refs.userForm.resetFields();
      }
    },
    
    // 
    resetPasswordForm() {
      if (this.$refs.passwordForm) {
        this.$refs.passwordForm.resetFields();
      }
    }
  }
};
</script>

<style scoped>
.roles-container {
  padding: 20px;
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.header-left {
  display: flex;
  align-items: center;
}

.back-button {
  cursor: pointer;
  color: #409EFF;
  font-size: 14px;
  margin-right: 15px;
}

.page-title {
  margin: 0;
  font-size: 24px;
  color: #333;
}

.search-form {
  background-color: #fff;
  padding: 20px;
  border-radius: 4px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

.toolbar {
  background-color: #f5f7fa;
  padding: 23px 0px 0px 20px;
  margin-bottom: 20px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.action-btn {
  margin-right: 10px;
  margin-bottom: 20px;
}

.edit-btn, .delete-btn {
  cursor: pointer;
  color: #409EFF;
  margin-right: 10px;
}

.delete-btn {
  color: #F56C6C;
}

.pagination {
  margin-top: 20px;
  text-align: right;
  padding: 0 20px 20px 0;
}

.user-form, .password-form {
  max-width: 100%;
}

/*  */
.el-input__inner {
  background-color: #fff !important;
  border: 1px solid #dcdfe6 !important;
}

.el-input__inner::placeholder {
  color: #333 !important;
  opacity: 0.7;
}

.el-select .el-input__inner::placeholder {
  color: #333 !important;
  opacity: 0.7;
}

/*  */
.el-select .el-input__inner {
  border: 1px solid #dcdfe6 !important;
}

/*  */
.el-input__inner:hover, 
.el-input__inner:focus {
  border-color: #409EFF !important;
}
.search-btn{
  background-color: #409EFF;
}
</style>
